.AssessmentCard {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  overflow: hidden;
  border-radius: 4px;
  background-color: #fff;
  color: rgba(0, 0, 0, 0.87);
  padding: 16px;
  width: 82%;
  height: 360px;
}

@media only screen and (min-width: 600px) {
  .AssessmentCard {
    width: 52%;
  }
}

/*//////////Card In///////////*/
.card-in-enter {
  transform: translateX(204%) scale(0.95);
  opacity: 0.8;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0),
    0px 0px 0px 0px rgba(0, 0, 0, 00), 0px 0px 0px 0px rgba(0, 0, 0, 0);
}

.card-in-enter-active {
  transform: translateX(102%) scale(0.95);
  opacity: 0.8;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0),
    0px 0px 0px 0px rgba(0, 0, 0, 00), 0px 0px 0px 0px rgba(0, 0, 0, 0);
  transition: 600ms;
}

.card-in-enter-done {
  transform: translateX(102%) scale(0.95);
  opacity: 0.8;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0),
    0px 0px 0px 0px rgba(0, 0, 0, 00), 0px 0px 0px 0px rgba(0, 0, 0, 0);
}

.card-in-exit {
  transform: translateX(102%) scale(0.95);
  opacity: 0.8;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0),
    0px 0px 0px 0px rgba(0, 0, 0, 00), 0px 0px 0px 0px rgba(0, 0, 0, 0);
}

.card-in-exit-active {
  transform: translateX(204%) scale(0.95);
  opacity: 0.8;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0),
    0px 0px 0px 0px rgba(0, 0, 0, 00), 0px 0px 0px 0px rgba(0, 0, 0, 0);
  transition: 600ms;
}

.card-in-appear {
  transform: translateX(102%) scale(0.95);
  opacity: 0.8;
  box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0),
    0px 0px 0px 0px rgba(0, 0, 0, 00), 0px 0px 0px 0px rgba(0, 0, 0, 0);
}

/*//////////Card In Center///////////*/

