.RadioButtonLabel {
  width: 100%;
}

.RadioButtonLabel input[type="radio"] {
  opacity: 0;
  position: fixed;
  width: 0;
}

.RadioButton {
  margin: 4px 0 !important;
  height: 56px;
}

.RadioButtonSelected {
  background-color: #51bc93 !important;
  color: white !important;
}

@media only screen and (min-width: 960px) {
  .RadioButtonLabel {
    width: 60%;
  }
}

@media only screen and (min-width: 1280px) {
  .RadioButtonLabel {
    width: 50%;
  }
}

@media only screen and (min-width: 1920px) {
  .RadioButtonLabel {
    width: 40%;
  }
}
