.assessment-grid {
  background-color: #51bc93;
  height: 72vh;
}

.cardsContainer {
  width: 100%;
  height: 360px;
}

.cardHeader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20%;
  margin-bottom: 8px !important;
}

.cardOptions {
  height: 80%;
}

.cardButton {
  margin: 4px 0 !important;
  height: 56px;
}

@media only screen and (min-width: 960px) {
  .cardButton {
    width: 60% !important;
  }
}

@media only screen and (min-width: 1280px) {
  .cardButton {
    width: 50% !important;
  }
}

@media only screen and (min-width: 1920px) {
  .cardButton {
    width: 40% !important;
  }
}

/*/////Animation/////*/
.assessment-out-enter {
  transform: translateX(0vw);
}

.assessment-out-enter-active {
  transform: translateX(-100vw);
  transition: 600ms;
}

.assessment-out-enter-done {
  transform: translateX(-100vw);
  transition: 600ms;
}
.assessment-out-exit {
  transform: translateX(-100vw);
}

.assessment-out-exit-active {
  transform: translateX(0vw);
  transition: 600ms;
}
